<template>
<!-- 这里是登录界面 -->
    <div class="register" :class="cla" >
        <div class="pleasereg">
            <div class="head">
               <p>欢迎注册&lt;海贼王&gt;免费账号</p> 
               <router-link to="#">登录</router-link>
            </div>
            <img class="imges" src="../../assets/1.gif" alt="">
            <div class="ipt">
                <p :class="ps1">{{p1}}</p>
                <input :class="input1" v-on:blur="change1" type="text"  minlength="3" maxlength="8" v-model="uname" placeholder="请输入用户名">
                <p :class="ps2">{{p2}}</p>
                <input :class="input2" v-on:blur="change2" type="password" minlength="6" maxlength="10" v-model="upwd" placeholder="请输入密码">
                
            </div>
            <button class="btn" @click="bttn">登录</button>
        </div>
        <div>
            <img style="margin-top:700px;" src="../../assets/img/last.png" alt="">
        </div>
    </div>

</template>

<script>
export default {
    data(){
        return{
            cla:"a",
            i:0,
            uname:"",
            upwd:"",
            // 两个提示框
            p1:"-",
            p2:"-",
            // 样式提示框
            ps1:"",
            ps2:"",
            input1:"",
            input2:"",
        }
    },
    created(){
        this.change();
        
    },
    methods:{
        change(){
            setInterval(()=>{
                if(this.i%2==0){
                    this.cla="b"
                }else{
                    this.cla="a"
                }
                this.i++;
            },5000)
        },
        bttn(){
            // 找到当前的用户名
            var n=this.uname;
            // 找到密码
            var p=this.upwd;
            // 验证通过后，我们发送ajax请求到服务器
            var url="login";
            var data=`uname=${n}&upwd=${p}`;
            this.axios.post(url,data).then(result=>{
                // 获取服务器返回的结果，暂时调到头界面
                this.$router.push("/");
            })
        },
        change1(){
            var n=this.uname;
            var url="login1";
            var data=`uname=${n}`;
            if(n.length<3){
               this.p1="请输入正确的格式";
            }else{
                this.axios.post(url,data).then(result=>{
                if(result.data.code=="-1"){
                    // 当前用户验证样式可以修改，内容无法更新进dom树中；现在问题是页面只渲染一次
                    this.ps1="aa2";
                    this.p1='用户名已存在';
                    this.input1="input11";
                }else{
                    this.ps1="aa1";
                    this.p1="用户名正确";
                    this.input1="input12";
                }
               
            }) 
            }
            
        },
        change2(){
            var p=this.upwd;
            var url="login2";
            var data=`upwd=${p}`;
            if(p.length<6){
               this.p2="请输入正确的密码格式";
               console.log(this.p2)
            }else{
                this.axios.post(url,data).then(result=>{
                if(result.data.code=="-1"){
                    // 当前用户验证样式可以修改，内容无法更新进dom树中；现在问题是页面只渲染一次
                    this.ps2="aa2";
                    this.p2='用户名不存在';
                    this.input2="input11";
                }else{
                    this.ps2="aa1";
                    this.p2="用户名正确";
                    this.input2="input12";
                }
               
            }) 
            }
            
        }
        
    }
}

</script>

<style scoped>
.imges{
    width: 4rem;
}
.register{
    background-attachment: fixed;
    text-align: center;
}
.a{
    background: url(../../assets/img/7.jpg) center top;
    /* 随着页面滑动，固定不变； */
    /* background-attachment: fixed; */
    width: 100%;
    height:1000px;
}
.b{
    background: url(../../assets/img/8.jpg) center top;
    width:100%;
    height:1000px;
}
.pleasereg{
    border:1px solid #0f0;
    width: 400px;
    height:400px;
    background:#ff0;
    opacity: 0.8;
    position:absolute;
    top:16rem;left:27rem;
}
.head{
    display: flex;
    padding:2rem;
    font-size:1rem;
    color:#f00;
    justify-content: space-between;
}
.ipt{
    width:20rem;
    display:flex;
    flex-direction: column;
    padding:2.5rem;
    
}
.ipt>input:nth-child(2){
    padding:0.5rem;
    margin-top:1rem;
    outline: none;
    display: block;
}
.ipt>input:last-child{
    padding:0.5rem;
    margin-top:1rem;
    outline: none;
    display: block;
}
.ipt>p{
    margin:0 0 -0.5rem 10rem;
    opacity: 0;
    display:block;
}

.btn{
    width: 20rem;
    height:2.5rem;
    cursor: pointer;
    background-color:lightgreen;
    border-radius: 5px;
    border:0px;
}
.aa1{
    color:lawngreen;
    opacity: 1!important;
}
.aa2{
    color:#f00;
    opacity: 1!important;
}
.input11{
    border:1px solid #f00;
}
.input12{
    border:1px solid #0f0;
}
</style>
