<template>
<!--首页的新闻模块-->
    <div class="three1" style="margin-top:1rem;">
        <p class="xinwen">新闻介绍>>>>></p>
        <div class="three" v-for="j in jsonj" :key="j">
            <div class="three_img" @click="cn">
                <img style="width:400px;height:200px;" :src="j.img" alt="">
            </div>
            <div class="three_write">
                <h3 class="h3">{{j.title1}}</h3>
                <p class="p">{{j.title2}}</p>
                <p class="p1">浏览量:<b>{{j.n}}</b></p>
            </div>
        </div>
         <div class="five">
                <ul id="five">
                    <li :class="{a:cclass==i}" v-for="(l,i) in li" :key="i" @click="add(i)">{{l}}</li>
                </ul>
            </div>
            <div style="width:100%;">
                <img src="../../assets/img/last.png" alt="">
            </div>
    </div>
    
</template>

<script>
export default {
    data(){
        return{
            n:0,
            jsonj:[],
            li:["1","2","3","4","...","50"],
            cclass:0,
        }
    },
    mounted(){
        this.getfjson()
    },
    methods:{
        cn(){
            this.n++;
        },
        getfjson(){
            this.axios.get('/fjson').then((res)=>{
                var result = res.data.data;
                this.jsonj=result;
            })
        },
        add(i){
            if(i==4){
                return;
            }
            this.cclass=i;
            if(i==0){
                this.axios.get('/fjson').then((res)=>{
                var result = res.data.data;
                this.jsonj=result;
            })
            }
            if(i==1){
                this.axios.get('/f1json').then((res)=>{
                var result = res.data.data;
                this.jsonj=result;
            })
            }
            if(i==2){
                this.axios.get('/f2json').then((res)=>{
                var result = res.data.data;
                this.jsonj=result;
            })
            }
            if(i==3){
                this.axios.get('/f3json').then((res)=>{
                var result = res.data.data;
                this.jsonj=result;
            })
            }
            if(i==5){
               this.$router.push("roli"); 
            }
            
        }
    },
    watch:{
        
    }
}
</script>

<style scoped>
.xinwen{
    display: flex;
    padding:1rem;
    color:#f00;
    font-size: 1.5rem;
    background-color:darkcyan;
    cursor: pointer;
}
.three{
    display: flex;
    align-items: center;
    margin-bottom:1rem;
}
.three_img{
    margin:2rem 0 2rem 2rem;
}
.three_write{
    width: 100%;
    height: 200px;
    /* display: box; */
    /* 将文本上下排列 */
    /* box-orient:vertical; */
    display: flex;
    flex-direction: column;
    justify-content:start;
    background-color: rgb(1, 67, 122);
    cursor: pointer;
}
.h3{
    padding:2rem;
    float: left;
    color:coral;
    font-size:1.5rem;
}
.h3:hover{
    color:#f00;
}
.p{
    padding: 1rem;
    float: left;
    color:#aaa;
    
}
.p1{
    float: left;
    padding: 1rem;
    clear: both;
    color:darkblue;
}
/* 最后一个 */
div.five>ul{
    display:flex;
    justify-content: center;
    list-style: none;
    margin-left:20rem;
    margin-bottom:5rem;
}
div.five>ul>li{
    padding:1rem 1.5rem 1rem 1.5rem;
    color:#f00;
    margin-right:1rem;
    cursor: pointer;
}
div.five>ul>li:not(:first-child):hover{
    background:url(../../assets/img/7.jpg) no-repeat 50% 90%;
    background-size:cover;
    color:#f0f;
    cursor: pointer;
}
.a{
    background:url(../../assets/img/8.jpg) no-repeat 50% 90%;
    background-size:cover;
    color:#f0f;
}
</style>
