<template>
    <div class="roll2">
        <div class="roll2_start">
            <div class="roll2_first" v-for="j in jsonj" :key="j">
                <img :src="j.img" alt="">
                <p>{{j.title1}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            jsonj:[],
        }
    },
  methods: {
    getfjson() {
      this.axios.get("/s2json").then(res => {
        var result = res.data.data;
        this.jsonj = result;
      });
    }
  },
  mounted() {
    this.getfjson();
  }
}
</script>

<style scoped>
.roll2_start{
    display: flex;
    justify-content: space-between;
}
.roll2_first>img{
    width:100px;height:100px;
    border-radius: 10px;
    cursor: pointer;
    transition:all 1s linear;
}
.roll2_first>img:hover{
    transform: scale(1.1);
}
.roll2_first>p{
    color:#666;
}
.roll2_first>p:hover{
    color:#ff0;
    cursor: pointer;
}
</style>
