<template>
<!-- 这里是首页第一部分悬停小图显示大图 -->
<div class="box">
    <div class="little_img" >
        <ul v-for="(img,i) in imgUrl" :key="i">
            <li @mouseover='getIndex(img.url,i)'>
                <img :class="{a:i==b}" class="cc" :src="img.url">
            </li>
        </ul>
    </div>
    <div class="goodDetails_name_img">
       <div class="gooddetails_img">
              <img :src="ImgUrl" style="width: 100%;height: 100%">
       </div>
     </div>
</div>
</template>

<script>
export default {
    data(){
        return {
          imgUrl: [
              { index:1, url: require('../../assets/img/1.jpg') },
              { index:2, url: require('../../assets/img/2.jpg') },
              { index:3, url: require('../../assets/img/3.jpg') },
              { index:4, url: require('../../assets/img/4.jpg') },
              { index:5, url: require('../../assets/img/5.jpg') },
              ],
            //大图片默认显示第一张
            ImgUrl:require('../../assets/img/1.jpg'),
            b:0,
            }
        },
        //点击小图片时将图片路径赋值给大图
        methods:{ 
            getIndex(img,i){
               this.ImgUrl = img;
               this.b=i;
             },
        },
        mounted(){
          // 这里设置一个定时器，让图片自动循环；************************************************************
              var cont=this.imgUrl.map(function(elem,i,arr){
                return arr[i].url;
              })
              var time=setInterval(()=>{
                  for(var key in cont){
                    if(this.b==key){
                      this.ImgUrl=cont[key];
                    }
                  }
                  if(this.b==4){
                      this.b=0;
                      return;
                  }
                  this.b++;
              },2000)
          },
        destroyed(){
            clearInterval(this.time);
        }
        
}
</script>

<style scoped>

.box{
  display: flex;
  justify-content: center;
}
.gooddetails_img{
 width:1000px;height:790px;
 overflow: hidden;
}
.little_img{
  display: flex;
  flex-direction: column;
  clear: both;
}
.little_img>ul>li{
  margin:0.2rem;
}
.cc{
 width: 150px; height: 150px;
 transition:all 1s linear;
 cursor: pointer;
}
.a{
  box-shadow:2px 0px 15px 15px #f00;
  transform: scale(1.1);
}

</style>
