<template>
  <!-- 这里是第二个页面下拉琴 -->
  <div class="first1">
    <div class="list" v-for="(l,i) in lists" :key="i">
      <div class="title" @click="toggle(i)">{{l.title}}</div>
      <ul class="item" v-show="l.show">
        <li v-for="(item,index) in l.items" :key="index">{{item}}</li>
      </ul>
    </div>
    <div>
      <p class="pp">角色介绍</p>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      lists: [
        {
          title: "海贼王",
          items: ["哥尔·D·罗杰", "西尔巴兹·雷利"],
          show: false
        },
        {
          title: "四皇",
          items: ["马歇尔·D·蒂奇", "香克斯", "凯多", "夏洛特·玲玲"],
          show: true
        },
        {
          title: "三大将",
          items: ["赤犬 萨卡斯基", "青雉 库赞", "黄猿 波鲁萨利诺"],
          show: false
        },
        {
          title: "七武海",
          items: [
            "鹰眼",
            "白胡子二世",
            "明哥",
            "熊",
            "甚平",
            "女帝",
            "月光莫利亚"
          ],
          show: false
        },
        {
          title: "路飞海贼团",
          items: [
            "蒙奇·D·路飞",
            "诺诺罗亚·索罗",
            "文斯莫克·山治",
            "妮可·罗宾",
            "娜美",
            "乌索普",
            "托尼托尼·乔巴",
            "弗兰奇",
            "布鲁克",
            "甚平"
          ],
          show: false
        }
      ]
    };
  },
  methods: {
    toggle(index) {
      // ES6的新语法，称为箭头函数， 它是语法糖。
      // 当箭头函数函数体中只有一条语句时，return语句可省略。
      this.lists.forEach((list, i) => (list.show = i == index));
      // 回调函数， 回调函数有三个形参，list当前循环中对象,i表示当前循环中的索引
      // arr 表示循环的数组本身
      this.lists.forEach(function(list, i, arr) {
        //     // console.log(arr[i]==list);
        return (list.show = i == index);
        if (i == index) {
          list.show = true;
        } else {
          list.show = false;
        }
      });
    }
  }
};
</script>

<!--css区-->
<style scoped>
.pp {
  color: #aaa;
  font-size: 1.5rem;
  box-shadow: 0px 6px 9px 12px #f00;
  display: inline-block;
  margin-top: 1rem;
}

ul {
  list-style: none;
  transition: all 1s linear;
}

li {
  padding-left: 10px;
  line-height: 2;
}

li:hover {
  background-color: #eee;
  color: #0f0;
}

.list {
  padding: 0 50px;
  background: rgb(45, 4, 46);
}

.list .title {
  border: 1px solid #dfa;
  padding: 10px 5px;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  font-size: 18px;
  font-weight: bold;
  color: #0ff;
  box-shadow: 4px 5px 11px 5px #000;
}

.list .item {
  box-shadow: 4px 5px 11px 5px #fff;
}
.item > li {
  color: #f00;
}
.item > li:hover {
  background-color: #aa0;
  cursor: pointer;
}
</style>