<template>
<!-- 这里是所有页面的头部 -->
    <div id="header">
        <!-- 这块是无用的 -->
        <div v-show="isshow==true" :class="divba1" class="divba">
                <div class="leftimg">
                    <img src="../assets/head1.jpg" alt="">
                </div>
                <p @click="router(i)" v-for="(pp,i) in ppp" :key="i" class="pppp" :class="divba1">{{pp}}</p>
                <div class="righthead">
                    <router-link to="login">登录</router-link>
                    <router-link to="register">注册</router-link>
                </div>
        </div>
        <div class="headhead">
            <img src="../assets/header.jpg" alt="">
        </div>
        <div class="nav">
            <div class="middles">
                <div class="leftimg">
                    <img src="../assets/head1.jpg" alt="">
                </div>
                <p v-on:mousemove="mid(i)" v-on:mouseout="midd()" @click="router(i)" v-for="(elem,i) in pinner" :key="i" v-bind:class="{ p1:i==current }">{{elem}}</p>
                <ul v-show="act" class="headli">
                    <li v-for="elem of headd" :key="elem">{{elem}}</li>
                </ul>
                <ul v-show="act1" class="headli1">
                    <li v-for="elem of headd1" :key="elem">{{elem}}</li>
                </ul>
            </div>
            <div class="righthead">
                <router-link to="login">登录</router-link>
                <router-link to="register">注册</router-link>
            </div>
        </div>
        
    </div>
    
</template>

<script>
export default {
    data(){
        return{
            current:0,
            pinner:["首页","角色介绍","回忆录","海贼团","新闻资讯"],
            headd:["路飞","索隆","香吉士","罗宾","娜美","布鲁克","乔巴","..."],
            headd1:["顶上战争","新世界","七武海","四皇","海贼王"],
            act:"",
            act1:"",
            class1:"",
            ppp:["首页","角色介绍","回忆录","海贼团","新闻资讯"],
            scroll:"",
            isshow:false,
            divba1:{
                divba2:false
            },
        }
    },
    methods:{
        // 配置路由跳转；
        router(i){
            this.current=i;
            if(i==0){
                this.$router.push("/");
            }
            if(i==1){
                this.$router.push("roli");  
            }
            if(i==2){
                this.$router.push("think");
            }
            if(i==3){
                this.$router.push("roll");
            }
        },
        mid(i){
            this.current=i;
            if(i==1){
                this.act="true";
            }else{
                this.act="";
            }
            if(i==2){
                this.act1="true";
            }else{
                this.act1="";
            }
        },
        midd(){
            this.act="";
            this.act1="";
        },
        menu(){
            this.scroll=document.documentElement.scrollTop;
            if(this.scroll>150){
                this.isshow=true;
                this.divba1.divba2=true;
            }else{
                this.isshow=false;
                this.divba1.divba2=false;
            }
        }
    },
    mounted(){
        window.addEventListener('scroll',this.menu)
    },
    destroyed(){
        window.removeEventListener('scroll',this.menu)
    }
}
</script>

<style scoped>
#header{
    background:rgba(102, 102, 102, 0.1);
}
.headhead{
    width: 100%;
}
.nav{
    display: flex;
    justify-content: space-between;
}
.leftimg>img{
    width:12rem;
}
.middles{
    display: flex;
    justify-content:space-around;
    align-items: center;
    color:lawngreen;
    
}
.middles>p{
    padding:0 2rem;
    cursor: pointer;
    font-size:1.5rem; 
    width: 8rem;
    height:4rem;
    line-height: 4rem;
}
.middles>p:hover{
    transition:all 1s linear;
    border-bottom:5px solid rgb(170, 26, 1);
}
.p1{
    background:url(../assets/1.gif) no-repeat 50% 60%;
    background-size:cover;
    color:#ff0;
}
.headli{
    position: absolute;
    color:rgb(0, 217, 255);
    font-size:1.3rem;
    font-weight:900;
    top:9rem;left:24rem;
    box-shadow:3px 3px 5px 7px #aaa;
    cursor: pointer;
    z-index: 99;
}
.headli>li{
    padding:0.5rem 4.5rem 0.5rem 4.5rem;
}
.headli>li:hover{
    box-shadow: 3px 3px 5px 5px #f00;
}
.headli1{
    position: absolute;
    color:rgb(0, 217, 255);
    top:9rem;left:36rem;
    box-shadow:3px 3px 5px 7px #aaa;
    cursor: pointer;
    z-index: 99;
}
.headli1>li{
    padding:0.5rem 4.5rem 0.5rem 4.5rem;
}
.headli1>li:hover{
    box-shadow: 3px 3px 5px 5px #0ff;
}

/*这是登录和注册*/
 .righthead{
     display:flex;
     align-items: center;
 }
  .righthead>:first-child{
      font-size:2rem;
        color:#0ff;
  }
    .righthead>:last-child{
        padding-left:1rem;
        font-size:2rem;
        color:aquamarine;
    }
    /* 这块是顶部显隐藏 */
    .divba{
        height:4rem;width:100%;  
        transition: all 1s linear;
        background-color: rgba(0, 119, 255,0.3);
        cursor: pointer;
        z-index: 999;
        position:fixed;
        top:0;
        opacity: 0;
        display: flex;
    }
    .pppp{
    width:8rem;height:4rem;line-height: 4rem;
    transition: all 1s linear;
    background-color: rgba(145, 255, 0, 0.8);
    border-radius: 5px;
    border: 1px solid #f0f;
    float: left;
    margin:0 1rem;
    /* position:fixed; */
    /* top:0; */
    opacity: 0;
    }
    .pppp:hover{
    box-shadow:5px 5px 5px 5px #f00;
    color:#fff;
    background:url(../assets/1.gif) no-repeat 50% 60%;
    background-size:cover;
    }
    .divba2{
        opacity: 1;
    }
</style>
