<template>
<!-- 这里是注册页面-->
    <div class="register" :class="cla">
        <div class="header">
            <img src="../../assets/img/one.jpg" alt="">
        </div>
        <div class="header_h1">
            <h1>创建账号</h1>
            <button>网易</button>
        </div>
        <div class="header_btn">
            <span>或</span>
        </div>
        <div class="header_p">
            <p>创建海贼王通行证，开启您的冒险旅程。</p>
        </div>
        <div class="body_input">
            <input @focus="input_1" @blur="input_11" v-model="input1" type="text" placeholder="身份证号码" :class="classs1" autofocus>
            <div :style="sty1"  class="display_n">请仔细核对并填写您真实有效的身份证号码。</div>
            <input @focus="input_2" @blur="input_12" v-model="input2" type="text" placeholder="姓名" :class="classs2">
            <input @focus="input_3" @blur="input_13" v-model="input3" type="email" placeholder="电子邮箱" :class="classs3">
            <input @focus="input_4" @blur="input_14" v-model="input4" type="password" placeholder="密码" :class="classs4">
            <div style="position:relative"><i class="cucao" :style="cucaoa">{{cucao1}}</i></div>
            <div :style="sty2" class="display_nb">
                <i class="i11" :class="ii1"></i><p :style="s1">使用8-16个字符。</p>
                <i class="i12" :class="ii2"></i><p :style="s2">使用至少一个数字字符以及一个字母字符。</p>
                <i class="i13" :class="ii3"></i><p :style="s3">请勿使用任何特殊字符。</p>
                <i class="i14" :class="ii4"></i><p :style="s4">请勿使用与您账号名称相似的密码。</p>
            </div>
            <input v-on:keyup.13="submit" @focus="input_5" @blur="input_15" v-model="input5" type="text" placeholder="手机号码" :class="classs5">
            <div :style="sty3" class="display_nnb">您可以通过该手机号码进行修改密码、解除密保等操作。<a href="#">了解详情</a></div>
            <!-- <input @focus="input_6" @blur="input_16" v-model="input6" type="text" placeholder="验证码" :class="classs6"> -->
                <input v-model:checked="isagree" type="checkbox" class="check" id="cc">
                <label class="span1" for="cc">我已阅读并接受</label>
                <span class="span2">隐私政策</span>
            
            <div class="last_one">
                <button :disabled="!isagree" @click="importantclick">免费注册海贼王通行证</button>
                <button @click="have">已经有通行证了？</button>
                <p>点击“免费注册海贼王通行证”即表示我同意并接受</p><span>海贼王网最终用户许可协议</span>
            </div>
            
            <!-- 底部末端 -->
            <div class="bottom_img">
                <img style="width:8rem" src="../../assets/img/one.jpg" alt="">
            </div>
            <div>
                <img src="../../assets/img/2.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            sty1:"display:none",
            sty2:"display:none",
            sty3:"display:none",
            //5个输入框
            input1:"",
            input2:"",
            input3:"",
            input4:"",
            input5:"",
            //6个样式框
            classs1:"",    //inputred红色边框样式
            classs2:"",
            classs3:"",
            classs4:"",
            classs5:"",
            // classs6:"",
            // 四个图片的样式
            ii1:"i1",
            ii2:"i1",
            ii3:"i1",
            ii4:"i1",
            // 以及后面p标签的字体样式
            s1:"",
            s2:"",
            s3:"",
            s4:"",   //   color:#202020    
            cucao1:"",  
            cucaoa:"",
            isagree:false,
            cla:"a",
            i:0,
        }
    },
    methods:{
        // 点击按钮跳转到登录界面
        have(){
            this.$router.push("login");
        },
        // 点击注册按钮，验证通过则注册；
        importantclick(){
            // 这是省份证的正则
            var sfzreg=/^\d{15}(\d\d[0-9x])?$/; 
            var namereg=/[\u4e00-\u9fa5]{3}/;
            var emailreg=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
            var upwdreg=/^[a-zA-Z0-9]{8,16}$/;
            var phonereg=/((\+86|0086)\s+)?1[3-8]\d{9}/;
            // 得到用户输入的内容;
            var usfz=this.input1;
            var uname=this.input2;
            var uemail=this.input3;
            var upwd=this.input4;
            var uphone=this.input5;
            // 验证用户输入，如果错误则提示；
            if(!sfzreg.test(usfz)){
                this.classs1="inputred";
                return;
            }else{
                this.classs1="inputgreen";
            }
            if(!namereg.test(uname)){
                this.classs2="inputred";
                return;
            }else{
                this.classs2="inputgreen";
            }
            if(!emailreg.test(uemail)){
                this.classs3="inputred";
                return;
            }else{
                this.classs3="inputgreen";
            }
            if(!upwdreg.test(upwd)){
                this.classs4="inputred";
                return;
            }else{
                this.classs4="inputgreen";
            }
            if(!phonereg.test(uphone)){
                this.classs5="inputred";
                return;
            }else{
                this.classs5="inputgreen";
            }
            var url="register";
           //var data=this.qs.stringify({usfz,uname,uemail,upwd,uphone});// (params)`usfz=${usfz}&uname=${uname}&uemail=${uemail}&upwd=${upwd}&uphone=${uphone}`\
           var data=`usfz=${usfz}&uname=${uname}&uemail=${uemail}&upwd=${upwd}&uphone=${uphone}`;
            this.axios.post(url,data).then(result=>{
                this.$router.push("login");
            }).catch(err=>{
                console.log(err);
            })
        },


        // 第一个输入框获得焦点时；
        input_1(){
            this.sty1="display:block";
            this.classs1="input11";
            //var a=this.input1;
            //
        },
        // 第一个输入框失去焦点时；
        input_11(){
            this.sty1="display:none"; 
            this.classs1="";       
        },
        input_2(){
            this.classs2="input11";
        },
        input_12(){
            this.classs2="";
        },
        //第三个输入框获得焦点
        input_3(){
            this.classs3="input11";
        },
        input_13(){
            this.classs3="";
        },
        //第4个输入框获得焦点
        input_4(){
            this.classs4="input11";
            this.sty2="display:block";
            this.cucao1="粗糙";
            this.cucaoa="color:#aaa";
            // var d=this.input4;
            // // 设置四个正则，判断输入的密码;
            // var d1=/^[a-zA-Z0-9]{8,16}$/;
            // var d2=/[0-9][a-zA-Z]{1}/;
            // var d3=/\w{1}/;
            // var d4=/\w{6}/;
            // if(d1.test(d)){
             
            //         this.ii1="i2";
              
                
            // }
        },
        input_14(){
            this.classs4="";
        },
        //第5个输入框获得焦点
        input_5(){
            this.classs5="input11";
            // 显示div
            this.sty3="display:block";
        },
        input_15(){
            this.classs5="";
            this.sty3="display:none";
        },
        // //第6个输入框获得焦点
        // input_6(){
        //     this.classs6="input11";
        // },
        // input_16(){
        //     this.classs6="";
        // },
        // 动态更新壁纸
        change(){
            setInterval(()=>{
                if(this.i%2==0){
                    this.cla="b"
                }else{
                    this.cla="a"
                }
                this.i++;
            },5000)
        },

    },
    watch:{
        input4(){
            var d=this.input4;
            // 设置四个正则，判断输入的密码;
            var d1=/^[a-zA-Z0-9]{8,16}$/;
            var d2=/[0-9][a-zA-Z]{1}/;
            var d3=/\w{1}/;
            var d4=/\w{6}/;
            // 验证第一个要求是否通过
            if(d1.test(d)){
              this.s1="color:#666";
              this.ii1="i2"; 
            }else{
                this.ii1="i1";
                this.s1="";
            }
            if(d2.test(d)){
              this.s2="color:#666";
              this.ii2="i2"; 
            }else{
                this.ii2="i1";
                this.s2="";
            }
            if(d3.test(d)){
              this.s3="color:#666";
              this.ii3="i2"; 
              this.cucao1="普通";
              this.cucaoa="color:#fff";
            }else{
                this.ii3="i1";
                this.s3="";
                this.cucao1="粗糙";
                this.cucaoa="color:#aaa";
            }
            if(d4.test(d)){
              this.s4="color:#666";
              this.ii4="i2"; 
            }else{
                this.ii4="i1";
                this.s4="";
            }
            if(d3.test(d)&&d4.test(d)){
                this.cucao1="优良";
                this.cucaoa="color:#0f0";
            }else{
                this.cucao1="普通";
                this.cucaoa="color:#fff";
            }
            if(d3.test(d)&&d4.test(d)&&d2.test(d)){
                this.cucao1="史诗";
                this.cucaoa="color:purple";
            }else{
                this.cucao1="优良";
                this.cucaoa="color:#0f0";
            }
            if(d3.test(d)&&d4.test(d)&&d2.test(d)&&d1.test(d)){
                this.cucao1="传说";
                this.cucaoa="color:gold";
            }else if(d3.test(d)&&d4.test(d)&&d2.test(d)){
                this.cucao1="史诗";
                this.cucaoa="color:purple";
            }else if(d3.test(d)&&d4.test(d)){
                this.cucao1="优良";
                this.cucaoa="color:#0f0";
            }else if(d3.test(d)){
              this.cucao1="普通";
              this.cucaoa="color:#fff";
            }else{
                this.cucao1="粗糙";
                this.cucaoa="color:#aaa";
            }
        },
        
    },
    created(){
        console.log(this.qs);
        this.change();
    }
    
}
</script>

<style scoped>
*{
    margin: 0;padding: 0;
}
.register{
    background-attachment: fixed;
    text-align: center;
}
.a{
    background: url(../../assets/img/7.jpg) center top;
    width: 100%;
    height:1600px;
}
.b{
    background: url(../../assets/img/8.jpg) center top;
    width: 100%;
    height:1600px;
}
.header{
    padding-top:3rem;
}
.header_h1{
    padding-top:2rem;
    color:#fff;
}
.header_h1>button{
    margin-top:2rem;
    border: none;
    border-radius: 1px;
    width:32rem;
    color: #fff;
    display: inline-block;
    padding: 10px 0;
    font-size: 15px;
        height: auto;
    background-color: #d60000;
}
.header_h1>button:hover{
    background-color: #e60404;
}
.header_btn{
    clear: both;
    display: block;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    margin: 40px 0;
}
.header_btn>span{
    position: relative;
    display: inline-block;
    font-size: 13px;
    color: #fff;
    color: rgba(255,255,255,.3);
}
.header_btn>span::before{
    content: "";
    width:230px;
    height: 1px;
    background-color: #fff;
    background-color: rgba(255,255,255,.1);
    position: absolute;
    top: 40%;
    right: 100%;
    margin-right: 20px;
}
.header_btn>span::after{
    content: "";
    width:230px;
    height: 1px;
    background-color: #fff;
    background-color: rgba(255,255,255,.1);
    position: absolute;
    top: 40%;
    left: 100%;
    margin-left: 20px;
}
.header_p>p{
    color:#aaa;
    font-size:1rem;
    margin-top:-1rem;
    margin-right:13rem;
}
.body_input{
    width: 100%;
    display: flex;
    /* 数轴排列 */
    flex-direction: column;
    align-items:center;
    margin-top:1rem;
}
.body_input>input{
    padding:0.8rem;
    width:30rem;
    height:1rem;
    background: #000;
    border:none;
    color:#fff;
    margin-bottom:1rem;
}

.body_input>input:hover{
    border:1px solid #666;
}
.display_n{
    z-index: 100;
    width:32rem;
    height:4rem;
    padding-right:8rem;
    line-height: 4rem;
    color:#ccc;
    background-color: #272e3b;
    border: 1px solid #3d434f;
    background-clip: padding-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.75);
    margin-bottom:1rem;
}
.input11{
    outline:2px solid #0d639f;
    border:none;
}
.inputred{
    outline:2px solid #f00;
    border:none;
}
.inputgreen{
    outline:2px solid lightgreen;
    border:none;
}
.display_nb{
    display:block;
    padding: 1rem;
    width: 32rem;
    background-color: #272e3b;
    border: 1px solid #3d434f;
    background-clip: padding-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.75);
    margin-bottom:1rem;
    position: relative;
    text-align: left;
}
.display_nb>p{
    padding: 0.4rem;
    margin-left:1rem;
    color:rgba(255,255,255,.8);
}
.i1{
    width: 14px;height:14px;
    background:url(../../assets/img/icon.png) no-repeat -32px -224px;
    position: absolute;
}
.i2{
    width: 14px;height:14px;
    background:url(../../assets/img/icon.png) no-repeat -144px -80px;
    position: absolute;
    background-color: #0f0;
}
.i11{
    top:25px;
}
.i12{
    top:58px;
}
.i13{
    top:91px;
}
.i14{
    top:124px;
}
.cucao{
    width: 3rem;
    position: absolute;
    top:-2.8rem;
    left:13rem;
}
.display_nnb{
    display:block;
    padding: 1rem;
    width: 32rem;
    background-color: #272e3b;
    border: 1px solid #3d434f;
    background-clip: padding-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 0 0 10px 1px rgba(0,0,0,0.75);
    margin-bottom:1rem;
    position: relative;
    text-align: left;
    color:#fff;
    font-size: 16px;
}
.check{
    width:1.5rem!important;
    cursor: pointer;
    margin-left:-30rem;
}
.span1{
    margin-left:-20rem;
    margin-top:-1.9rem;
    color: #fff;
}
.span2{
    margin-left:-8rem;
    margin-top:-1.3rem;
    color:#00aeff;
    cursor: pointer;
}
.span2:hover{
    color:#fff;
}


.last_one{
    margin-top:1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.last_one>button{
    width: 32rem;
    padding:0.5rem;
}
.last_one>button:first-child{
    background: #0e86ca;
    color:#fff;
    border:#0e86ca;
    margin-bottom:1rem;
}
.last_one>button:first-child:hover{
    background: #10a1de;
}
.last_one>button:nth-child(2){
    background-color:rgba(0,0,0,0.4);
    border:1px solid #0e86ca;
    color:#0e86ca;
    margin-bottom:2rem;
}
.last_one>button:nth-child(2):hover{
    color:#91e4ff;
    
}
.last_one>p{
    margin-left:-12rem;
    font-size:0.96rem;
    color:#ced5d9;
}
.last_one>span{
    margin-left:21.2rem;
    margin-top:-1.3rem;
    font-size:0.96rem;
    color:#0e86ca;
    cursor: pointer;
}
.last_one>span:hover{
    color:#fff;
}


/*底部末端*/
.bottom_img{
    margin-bottom:2rem;
}
.bottom_img>img{
    margin-top:5rem;
}
</style>
